.factory{
    border-style: groove;
    border-color: rgb(199, 199, 192);
    border-width: .3em;
    min-height: 400px;
    box-shadow: inset -2em 2em 2em black;
    max-width: 90%;
    background-image: url("/krzysztof-kowalik-djdoRnnKLyo-unsplash.jpg");
    background-size: cover;
    background-position: 50%;
}
.rnd-btn{
    margin:1em;
    width: 250px
}
.robotBlock{
    /* For testing */
    border-style: dashed;
    border-color:greenyellow;
    border-width: 4px;

}
.roboContainer{
    width: 200px;
    height: 300px;
    position: relative;
    margin: auto;
    margin-top: 3em;
    
}
.roboPosition {
    width: 100%;
    height: 50%;
    position: relative;
    margin: auto;
    border-color: none;
    top: -.5em;
    margin-top: 6em;
}
.headPosition{
    width: 100%;
    height: auto;
    position: absolute;
    top: -125px;
    margin: auto;
    z-index: 1;
}
.head{
    position: absolute;
    margin: auto;
    width: 100%;
    bottom:010px;
    left:0
}
.facePosition{
    position: absolute;
    width: 70px;
    top: 40px;
    left: 33%;
}
.dome-head {        
    -webkit-mask-image: url(/robotParts/heads/dome-vector.svg);
    mask-image: url(/robotParts/heads/dome-vector.svg);
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
    position: absolute;
}
.sq01-head {
    -webkit-mask-image: url(/robotParts/heads/sq01-vector.svg);
    mask-image: url(/robotParts/heads/sq01-vector.svg);
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
 position: absolute;
}
