    position: absolute;
    }
.leftArm {
    position: absolute;
    top:12px;
    left:-65px;
    -webkit-transform-origin: 85% 12.5%;
    -moz-transform-origin: 85% 12.5%;
    -ms-transform-origin: 85% 12.5%;
    -o-transform-origin: 85% 12.5%;
    transform-origin: 85% 12.5%;
}
.rightArm{
    -webkit-transform: scaleX(-1);
    transform: scaleX(-1);
    position: absolute;
    top: 12px;
    right: 8px;
    -webkit-transform-origin: 85% 12.5%;
    -moz-transform-origin: 85% 12.5%;
    -ms-transform-origin: 85% 12.5%;
    -o-transform-origin: 85% 12.5%;
    transform-origin: 85% 12.5%;
}